import { CommonModule } from "@angular/common";
import { Component, EventEmitter, Input, NgModule, Output, TemplateRef, ViewChild, ViewContainerRef } from '@angular/core';
import { FormsModule } from "@angular/forms";
import { NgZorroAntdModule } from 'ng-zorro-antd';
import { DirectivesModule } from '../../directives/yzt-directives.modules';
import { GridUtilService } from './share/grid-util.service';
import * as _ from 'lodash';

export interface PageData {
  content: Array<any>;
  numberOfElements: number;
  [portName: string]: any;
}

class GridIconIF {
  field: '';
  prop: '';
  iconTemplate: TemplateRef<any>
}

export interface PageIndexAndSize {
  page: number,
  size: number
}

@Component({
  selector: `yzt-grid`,
  templateUrl: `yzt-grid.component.html`,
  styleUrls: [`yzt-grid.component.scss`]
})
export class UIGridComponent {
  @ViewChild('gridImg', { read: ViewContainerRef }) gridImg: ViewContainerRef;
  @ViewChild('nzTable') nzTable: any;
  _scroll: { x: string; y: string } = { x: null, y: null };
  _data: PageData = { content: [], numberOfElements: 0 };
  _dataSet = [];
  _selections: any;
  _loading = false;
  set loading(v: boolean) {
    this._loading = !!v;
  }
  _fixScrollY: string | number;
  _title = '';
  _titleTpl: TemplateRef<any>; // thead
  _extraTitleTpl: TemplateRef<any> = null; // theadtbody
  _custom_popoverTpl: TemplateRef<any>;
  _exportLoading = false;
  _editCol = false;
  //
  targetColumns: any[] = [];
  //columns
  editColumns: any[] = [];
  //grid
  buttonGather = {
    showEditColumn: false,
    enableExport: false,
  }
  // 
  _sortName = null;
  _sortValue = null;

  set pageIndex(num) {
    if (this._page === num) return;
    this._page = num

  }
  get pageIndex() {
    return this._page;
  }

  // 
  @Input() set pageSize(num) {
    if (this._size === num) return;
    this._size = num
  }
  get pageSize() {
    return this._size;
  }

  _page = 1;
  _size = 10;
  // 
  private _pageIndex = 1;
  private _pageSize = 1;
  /**
   * 
   */
  _allChecked = false;
  _indeterminate = false;
  _displayData = [];
  // 
  _iconComp = {};
  // 
  _customComp: { [k: string]: any } = {};
  // 
  _searchCustom: { [k: string]: TemplateRef<any> } = {};
  _hasSearch: boolean;
  // 
  _popoverStr = '';
  _popoverArr = [];
  _fieldGather = {}

  set editCol(show: boolean) {
    if (show) {
      this.editColumns = [...this.columns, ...this.targetColumns];
    }
    this._editCol = show;
  }

  @Output() load: EventEmitter<PageIndexAndSize> = new EventEmitter();
  @Output() selectionChange: EventEmitter<any> = new EventEmitter();
  @Output() cellClick: EventEmitter<any> = new EventEmitter();
  @Output() cellOver: EventEmitter<any> = new EventEmitter();
  @Output() exportCSV: EventEmitter<any> = new EventEmitter();
  @Output() filterData: EventEmitter<any> = new EventEmitter(); // 

  @Input() id: string;
  @Input() customClass: string;
  @Input() columns = [];
  @Input() showSizeChanger = false; // 
  @Input() showPagination = true; // 
  @Input() pageSizeValues = [10, 30, 50, 100];
  @Input() showTitle = true;
  @Input() mulitipy = false; // 
  @Input() hideCheck = false; // 
  @Input() isSyncData = true;
  // usage: {field: "status", normal: "normal"},
  // normalstatus
  @Input() colColor = {
    field: "", // 
    normal: "",
    unnormal: "",
    // 
    prewarning: "",
    warning: "",
    timeOut: "",
    // 
    delete: ""
  }

  @Input()
  set scroll(value: { x: string; y: string }) {
    if (value) {
      this._scroll = value;
    } else {
      this._scroll = { x: null, y: null };
    }
    //   this.cdr.detectChanges();
  }

  @Input()
  set data(value: PageData) {
    setTimeout(_ => {
      this._loading = false;
    })
    if (!value) return;
    // 
    if (value.size) {
      this.pageSize = value.size;
      this.pageIndex = value.number + 1;
      this._pageIndex = this.pageIndex; // pageIndexChange
      this._pageSize = this.pageSize; // pageSizeChange
    }
    let content = value['content'] || [];
    // 
    for (let row of content) {
      const columns = [].concat(this.columns).concat(this.targetColumns);
      for (let c of columns) {
        let fieldValue = _.get(row, c.field, ""); // eg:c.field="shipper.clientType"
        if (!c.field) {
          continue;
        }
        row[c.field] = fieldValue;
        let textLen = Number(c.textLength) ? c.textLength : 20;
        // 
        if (fieldValue.length > textLen) {
          row[c.field + '_short_'] = this.replaceTextOmit(fieldValue, textLen, '')
        }
        // mergeName 
        if (c.popover) {
          row[c.field + '_popover_'] = this.replaceTextOmit(fieldValue, textLen, c.popover)
          // _.set(row, c.field + '_popover_arr_', this.splitName(fieldValue, c.popover));
        }
      }
    }
    this._displayData = value['content'] = content;
    this._data = { ...value };
    // 
    if (this._sortName && this._sortValue) this.search();
  }

  get data(): PageData {
    return this._data;
  }

  /*  get fieldValue(row:Object,fieldName:string){
      if()
   } */


  @Input()
  set fixScroll(height: any) {
    this._fixScrollY = `${parseInt(height)}px`;
  }

  // 
  @Input()
  set title(value: string | TemplateRef<void>) {
    if (value instanceof TemplateRef) {
      this._titleTpl = value;
    } else {
      this._title = value;
    }
  }

  // ,theadtbody
  @Input()
  set extraTitle(value: string | TemplateRef<void>) {
    if (value instanceof TemplateRef) {
      this._extraTitleTpl = value;
    }
  }

  // 
  @Input() set csPopover(value: TemplateRef<void>) {
    this._custom_popoverTpl = value;
  }

  // 
  @Input()
  set searchTmpl(templs: any) {
    if (Array.isArray(templs)) {
      let ref;
      templs.forEach((templ, i) => {
        ref = Object.keys(templ._def.references)[0].split('_')[1].toString();
        Object.defineProperty(this._searchCustom, ref, {
          value: templ
        })
      })
    }
    this._hasSearch = arguments ? true : false;

  }

  @Input()
  set selection(value: Array<any>) {
    // 
    if (!this.util.isEqual(this._selections, value)) {
      if (!value.length) {
        this._displayData.forEach(data => {
          data.checked = false;
        });
        this._indeterminate = false;
        this._allChecked = false;
      }
      this._selections = value;
      this.selectionChange.emit(value);
    }
  }


  constructor(private util: GridUtilService,
    public _vcr: ViewContainerRef,
  ) {
  }

  ngOnInit() {
    if (this.id) {
      let columnsMap = localStorage[this.id];
      if (columnsMap) {
        columnsMap = JSON.parse(columnsMap);
        let sourceColumns = columnsMap["sourceColumns"];
        let targetColumns = columnsMap["targetColumns"];
        if (sourceColumns && targetColumns && this.util.generateColumnKey([...sourceColumns, ...targetColumns]) === this.util.generateColumnKey(this.columns)) {
          this.columns = sourceColumns;
          this.targetColumns = targetColumns;
        }
      }
    }
    this.editColumns = this.columns;
    this.editColumns.forEach((column, i) => {
      Object.defineProperties(column, {
        title: { value: column.header },
        disabled: { value: column.transferVisabled || false }
      })
    })
  }

  ngAfterViewInit() {
    setTimeout(_ => {
      this.onLazyLoad();
    })
  }

  ngOnChanges() {
  }

  ngOnDestroy() {
    if (this.id) {
      localStorage[this.id] = JSON.stringify({
        sourceColumns: this.columns,
        targetColumns: this.targetColumns
      });
    }
  }

  /**page index change event handler */
  nzPageIndexChange($event) {
    setTimeout(() => {
      if (this._pageIndex === $event) {
        return;
      }
      this.onLazyLoad();
      this._pageIndex = $event;
    });
  }

  pageSizeChange($event) {
    // sizepageIndex
    if (this._pageIndex !== this.pageIndex) {
      return;
    }
    setTimeout(() => {
      if (this._pageSize === $event) {
        return;
      }
      this.onLazyLoad();
    });
  }

  isNeedSearch(column: any) {
    for (let value in column) {
      if (column[value] && column[value].indexOf('search_') !== -1)
        return column[value].split('_')[1];
    }
    return 0;
  }

  editChange(change: any) {
    let originChange = change,
      items = originChange.list;

    items.forEach((item, index) => {
      if (change.from === 'left') {
        this.editColumns.forEach(column => {
          if (item['field'] === column['field'] && column.direction !== 'right')
            column.direction = 'right';
        })
      } else {
        this.targetColumns.forEach(column => {
          if (item['field'] === column['field'] && column.direction === 'right')
            column.direction = 'left';
        })
      }
    })

    this.columns = this.editColumns.filter(column => column.direction !== 'right');
    this.targetColumns = this.editColumns.filter(column => column.direction === 'right');
    // 
    this.editColumns = [...this.columns, ...this.targetColumns];
  }

  onLazyLoad(page: PageIndexAndSize = { page: this.pageIndex, size: this.pageSize }): any {
    /*   if (this.isSyncData) {
        this._loading = true;
      } */
    this.load.emit(page);
  }

  getIconInstance({ outField, outProp, iconTemplate }) {
    this._iconComp[outField] = {
      outField: outField || '',
      outProp: outProp || '',
      iconTemplate: iconTemplate || '',
    }
  }

  getCusTmplInstance({ outField, customTemplate }) {
    this._customComp[outField] = {
      outField: outField || '',
      customTemplate: customTemplate || ''
    }
  }

  /**
   * 
   * @param rows
   */
  onRowSelectChange(data: any, index, pageData) {
    if (this.mulitipy) {
      pageData.map(item => {
        item.checked = false;
      })
      data.checked = !data.checked;
    } else {
      for (let i = 0; i < pageData.length; ++i) {
        if (i === index && data.checked) return;
      }
      data.checked = !data.checked;
    }

    this.mulitipy ? this.refreshStatus() : this.refreshSingleStatus(null, this._data.content, index);

    return false;
  }

  /**
   * checkbox
   */
  refreshStatus(event?: MouseEvent) {
    if (event) {
      event.stopPropagation();
    }
    const selections = this._displayData.filter(value => value.checked === true);
    const allChecked = this._displayData.every(value => value.checked === true);
    const allUnChecked = this._displayData.every(value => !value.checked);
    this._allChecked = allChecked;
    this._indeterminate = (!allChecked) && (!allUnChecked);
    this.selection = selections;
  }

  refreshSingleStatus(event: MouseEvent, data, index: number) {
    if (event) {
      event.stopPropagation();
    }
    data.forEach((val, i) => {
      if (index === i) return;
      val.checked = false;
    })
    this.selection = data.filter(v => v.checked);
  }

  /**
   * 
   * @param value
   * @param data
   */
  checkAll(value, data: Array<any>) {
    if (value) {
      this._displayData.forEach(data => {
        data.checked = true;
      });
    } else {
      this._displayData.forEach(data => {
        data.checked = false;
      });
    }
    this.refreshStatus();
  }


  /**
   * cell
   * @param event
   * @param row
   * @param field
   */
  onCellClick(event: Event, row: any, field: any) {
    event.stopPropagation();
    let value = row[field];
    this.cellClick.emit({
      row: row,
      field: field,
      value: value,
      originalEvent: event
    });
  }

  /**
   * mouseenter
   */
  onCellMouseEnter(event: any, row: any, field: any) {
    event.stopPropagation();
    // let value = this.value(row, field);
    let value = row[field];
    this.cellOver.emit({
      row: row,
      field: field,
      value: value,
      originalEvent: event
    });
  }

  /**
   * 
   * @param val
   * @returns {any}
   * @constructor
   */
  dataToStr(val: any) {
    let resultData;
    if (typeof val == 'number') {
      resultData = val.toString();
    } else if (typeof val == 'undefined') {
      resultData = '';
    } else if (val == null) {
      resultData = '';
    } else if (typeof val == 'object') {
      resultData = JSON.stringify(val);
    } else if (typeof val == 'boolean') {
      resultData = val ? '' : '';
    } else {
      resultData = val;
    }
    return resultData;
  }

  /**
   * textLengthcolumn
   * @param val
   * @param textLength
   * @param mergeName popover,key,eg: [{a: ''},{a: ''}] ,mergeName'a'
   * @returns {string|void|any}
   */
  replaceTextOmit(val: any, textLength: number = 20, mergeName = '') {
    let resultData, temp;
    resultData = this.dataToStr(val);
    if (Object.prototype.toString.call(val) === '[object Array]') {
      let namesArr = _.map(val, mergeName),
        namesStr = namesArr.join(',');
      temp = namesStr.slice(0, textLength);
      return namesStr.length > textLength ? `${temp}...` : namesStr;
    }

    if (typeof resultData === 'string') {
      temp = resultData.slice(0, textLength);
      return resultData.length > textLength ? `${temp}...` : resultData;
    } else {
      return resultData;
    }
  }

  /**
   * , popover
   * popover
   * htmlval
   */
  splitName(val, mergeName = '') {
    let result;
    if (Object.prototype.toString.call(val) === '[object Array]') {
      let namesArr = _.map(val, mergeName),
        namesStr = namesArr.join(',');
      result = namesStr;
    } else if (typeof val === 'string') {
      result = val;
    }
    let reg = /[,]/g;
    result = result.replace(reg, ',');
    return _.compact(result.split(',')) || [];
  }

  /**
   *
   * @param grid
   * @param data
   * @param isFailed 
   */
  doExportCSV(grid, data, isFailed?: boolean) {
    grid.exportDisable = false;
    if (isFailed) {
      return;
    }
    let columns = grid.columns;
    var csv = '\ufeff';
    //headers
    for (var i = 0; i < columns.length; i++) {
      if (columns[i].field && !columns[i].hidden) {
        csv += '"' + (columns[i].header || columns[i].field) + '"';
        if (i < (columns.length - 1)) {
          csv += ",";
        }
      }
    }
    //body
    data.forEach(function (record, i) {
      csv += '\n';
      for (var i_1 = 0; i_1 < columns.length; i_1++) {
        if (columns[i_1].field && !columns[i_1].hidden) {
          let value = Object.defineProperty(record, columns[i_1].field, null);
          if (typeof value === 'string') {
            value = value.replace('"', '""');
          } else if (value === null || value === 'null' || value === 'undefined') {
            value = "";
          }
          if (!isNaN(Number(value)) && value.length > 12) {
            csv += '"' + value + '\ufeff"';
          } else {
            csv += '"' + value + '"';
          }
          if (i_1 < (columns.length - 1)) {
            csv += ",";
          }
        }
      }
    });
    var blob = new Blob([csv], {
      type: 'text/csv;charset=utf-8;'
    });
    if (window.navigator.msSaveOrOpenBlob) {
      navigator.msSaveOrOpenBlob(blob, '.csv');
    }
    else {
      var link = document.createElement("a");
      link.style.display = 'none';
      document.body.appendChild(link);
      if (link.download !== undefined) {
        link.setAttribute('href', URL.createObjectURL(blob));
        link.setAttribute('download', '.csv');
        link.click();
      }
      else {
        csv = 'data:text/csv;charset=utf-8,' + csv;
        window.open(encodeURI(csv));
      }
      document.body.removeChild(link);
    }
  }

  exportDisable = false;

  exportCSVIntenal() {
    let $this = this;
    this.exportDisable = true;
    this.exportCSV.emit({
      done: $this.doExportCSV,
      grid: $this
    });
  }

  /**
   * colColor
   * @param status 
   */
  getColColor(status) {
    if (!this.colColor.field) return;
    switch (status) {
      case this.colColor.normal:
        return "#000000a6";
      case this.colColor.delete:
        return "#f04134";
      // case this.colColor.prewarning:
      //     return "origin";
      // case this.colColor.warning:
      //     return "green";
      // case this.colColor.timeOut:
      //     return "#000";
      default:
        return "#000000a6";
    }
  }

  // 
  search() {
    let searchObj = {};
    for (let field in this._fieldGather) {
      if (this._fieldGather[field].trim()) {
        Object.defineProperty(searchObj, field, {
          value: this._fieldGather[field],
          writable: true,
          enumerable: true,
          configurable: true
        });
      }
    }
    if (!Object.keys(searchObj).length) {
      this._displayData = [...this.data['content']];
    } else {
      this._displayData = this.data['content'].filter(item => {
        let flag = false;
        for (let key in searchObj) {
          if (item[key].toString().indexOf(searchObj[key]) !== -1) {
            flag = !flag;
            break;
          }
        }
        return flag;
      })
    }
    // https://ng-zorro.github.io/components/table/zh#components-table-demo-head
    if (this._sortName && this._sortValue) {
      this._displayData = this._displayData.sort((a, b) => (this._sortValue === 'ascend') ? (a[this._sortName] > b[this._sortName] ? 1 : -1) : (b[this._sortName] > a[this._sortName] ? 1 : -1));
    }
    this.filterData.emit({ content: this._displayData });
  }

  // 
  sort(sort: { key: string, value: string }): void {
    this._sortName = sort.key;
    this._sortValue = sort.value;
    this.search();
  }
}

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    NgZorroAntdModule,
    DirectivesModule
  ],
  declarations: [
    UIGridComponent
  ],
  providers: [
    GridUtilService
  ],
  exports: [UIGridComponent]
})
export class UIGridModule { }
