export interface ColumnInter {
    field: string;
    [propName: string]: any;
}

import { Injectable } from "@angular/core";

@Injectable()
export class GridUtilService {
    /**
     * '|'
     * @param arr 
     */
    generateColumnKey(columns: Array<ColumnInter>): string {
        let fields = [];
        for (let column of columns) {
            fields.push(column.field);
        }
        fields.sort();
        let key = "";
        for (let field of fields) {
            if (key.length > 0) {
                key += "|";
            }
            key += field;
        }
        return key;
    }

    /**
     * 
     * @param obj 
     */
    cloneDeep(obj) {
        let str, newobj = obj.constructor === Array ? [] : {};
        if (typeof obj !== 'object') {
            return;
        } else if ((<any>window).JSON) {
            str = JSON.stringify(obj), //
                newobj = JSON.parse(str); //
        } else {
            for (var i in obj) {
                newobj[i] = typeof obj[i] === 'object' ? this.cloneDeep(obj[i]) : obj[i];
            }
        }
        return newobj;
    };

    /**
     * isEqual
     * @param a 
     * @param b 
     */
    isEqual(a, b) {
        //ab
        if (a === b) {
            //0-0
            return a !== 0 || 1 / a === 1 / b;
        }
        //nullundefined
        if (a == null || b == null) {
            return a === b;
        }
        //ab
        var classNameA = toString.call(a),
            classNameB = toString.call(b);
        //false
        if (classNameA !== classNameB) {
            return false;
        }
        //
        switch (classNameA) {
            case '[object RegExp]':
            case '[object String]':
                //
                return '' + a === '' + b;
            case '[object Number]':
                //,NaN
                if (+a !== +a) {
                    return +b !== +b;
                }
                //0-0
                return +a === 0 ? 1 / +a === 1 / b : +a === +b;
            case '[object Date]':
            case '[object Boolean]':
                return +a === +b;
        }
        //
        if (classNameA == '[object Object]') {
            return this.objectIsEqual(a, b)
        }

        //
        if (classNameA == '[object Array]') {
            if (a.length !== b.length) return false;
            if (a.toString() === '[object Object]' && b.toString() === '[object Object]') {
                for (let i = 0; i < a.length; i++) {
                    let item1 = a[i];
                    let item2 = b[i];
                    if (this.objectIsEqual(item1, item2)) {
                        return true;
                    }
                }
                return false;
            } else if (a.toString() == b.toString()) {
                return true;
            }
            return false;
        }
    }

    objectIsEqual(a, b) {
        //ab
        var propsA = Object.getOwnPropertyNames(a),
            propsB = Object.getOwnPropertyNames(b);
        if (propsA.length != propsB.length) {
            return false;
        }
        for (var i = 0; i < propsA.length; i++) {
            var propName = propsA[i];
            //false
            if (a[propName] !== b[propName]) {
                return false;
            }
        }
        return true;
    }
}
